@use '../utils/mixins' as *;

.header {
  background-color: #292929;
  background-image: url(../images-vectors/photos/background-photo-phone.jpg);
  background-repeat: no-repeat;
  background-size: cover;

  min-height: 100vh;
  padding: 0 20px 94px;
  box-sizing: border-box;

  grid-template-rows: 1fr 1fr;

  display: grid;
  justify-content: center;
  grid-template-columns: repeat(2, 130px);
  gap: 20px;

  @include for-desktop() {
    grid-template-columns: repeat(12, 63px);
    gap: 24px;
  }

    &__navigation {
      align-self: start;
      grid-column: 1 / 3;
      grid-row: 1 / 2;

      @include for-desktop() {
        grid-column: 1 / 13;
      }
    }

    &__title {
      align-self: end;
      grid-column: 1 / 3;
      grid-row: 2 / 3;

      @include for-desktop() {
        grid-column: 1 / 13;
      }
    }
}
