@use '../utils/mixins' as *;

.main {
  padding: 100px 20px;

  display: grid;
  justify-content: center;
  grid-template-columns: repeat(2, 130px);
  gap: 20px;

  @include for-desktop() {
    grid-template-columns: repeat(12, 63px);
    gap: 24px;
  }

  &__title {
    margin-bottom: 48px;
    grid-column: 1 / 3;
  }

  &__content {
    grid-column: 1 / 3;
  }

  &__whyus {
    margin-bottom: 24px;
    text-align: left;
    grid-column: 1 / 3;
  }

  &__contact {
    margin-bottom: 40px;
    grid-column: 1 / 3;
  }

  &__section {
    grid-column: 1 / 3;
  }
}
